.container-edge {
  margin: 0 auto;
  padding-left: 249px;
  padding-right: 249px;

  &--header {
    padding-left: 4.4%;
    padding-right: 4.4%;
  }

  &--advantage {
    padding-left: 19%;
    padding-right: 19%;
  }

  &--service {
    padding-left: 18%;
    padding-right: 18%;
  }

  &--form {
    padding-left: 18.8%;
    padding-right: 18.8%;
  }

  &--footer {
    padding-left: 4.4%;
    padding-right: 4.4%;
  }
}

@media (max-width: $tablet-width) {
  .container-edge {
    padding-left: 25px;
    padding-right: 25px;

    &--header {
      padding-left: 25px;
      padding-right: 25px;
    }

    &--advantage {
      padding-left: 25px;
      padding-right: 25px;
    }

    &--footer {
      padding-left: 25px;
      padding-right: 24px;
    }
  }
}

@media (max-width: $mobile-width) {
  .container-edge {
    padding-left: 15px;
    padding-right: 15px;
  }
}
